import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponent implements OnInit {

  front=['pug','html5', 'coffeescript', 'angular', 'vue', 'stylus', 'gulp', 'bootstrap']
  backend=['themosis', 'laravel', 'wordpress', 'mysql', 'elixir']
  display = false
  frontendStatus = true
  backendStatus = false
  increment = 0
  max = 0

  constructor() { }

  setButtonStatus(origin): void {
    if (origin=='back') {
      this.frontendStatus = false
      this.backendStatus = true
    } else if (origin == 'front') {
      this.frontendStatus = true
      this.backendStatus = false 
    }
  }

  circle(words): void {
    var length = words.join().length
    this.increment = 360/length
    return words.join().replace(/,/g,'_').split('')
  }


  ngOnInit() {
  }

}


